##{$alias}app {

	*:focus {
		box-shadow: none;
	}
	.plugin-card .name {
		margin-left: 102px;
	}
	.plugin-icon {
		width: 80px;
		height: 80px;
	}
	.product-price {
		font-weight: bold;
		font-size: 16px;
	}
	.star-rating > div {
		display: inline-block;
	}
	.num-ratings {
		display: inline-block;
		padding-left: 5px;
	}
	.plugin-card .desc {
		margin-left: 100px;
		margin-right: 100px;
	}
	.plugin-card h3 a {
		text-decoration: none;

		&:hover {
			text-decoration: underline;
		}
	}
	.column-rating {
		max-width: 250px;
	}
	.column-updated {
		width: calc(100% - 250px);
		font-weight: bold;
	}
	.column-description {
		overflow: hidden;
	}

	.filter-links {
		width: 100%;

		li.copy-aateam {
			position: relative; 
			right: 10px;
			float: right;
			display:inline-block;
		}
	}

	.title_line {
		background-color: #fff;
		padding: 25px 15px;
		margin-top: 20px;
	}
	.aat-icon {
		width: 163px;
		height: 38px;
		display: inline-block;
		background-image: url('../img/logo-aat.png');
		position: relative;
		left: 10px; 
	}
	.button-help {
		float: right;
		position: relative;
		top: 10px; 
		right: 10px;
	}

	.#{$alias}button {
		border: 1px solid #FDD835;
		margin-right: 10px;
		background: #FFEB3B;
		box-shadow: none;
		text-shadow: none;
		padding: 2px 20px;
		height: auto;
		color: #000;
		font-weight: bold;

		&:hover {
			background: #5E35B1;
			border: 1px solid #4527A0;
			color: #fff;
		}

		&.button-secondary {
			color: #fff;
			background: #BDBDBD;
			border: 1px solid #9E9E9E;

			&:hover {
				background: #9E9E9E;
			}
		}
	}

	.item-screenshots {
		padding-right: 40px;
		position: relative;

		&:after {
			content: "";
			position: absolute;
			top: 6px;
			right: 10px;
			width: 19px;
			height: 19px;
			background-image: url('../img/if_32_171485.svg');
			background-size: 19px 19px;
		}
	}

	.plugin-lightbox {
		position: fixed;
		top: 35px;
		left: 0;
		right: 0;
		bottom: 30px;
		z-index: 99999;

		&:after {
			position: absolute;
			top: -50px;
			left: 0;
			right: 0;
			bottom: -50px;
			background: rgba( 0, 0, 0, 0.6 );
			content: "";
			z-index: 99;
		}
	}

	#plugin-container-wrapper[data-name="description"] .#{$alias}item-desc {
		display: block !important;
	}
	#plugin-container-wrapper[data-name="attributes"] .#{$alias}item-attributes {
		display: block !important;
	}
	#plugin-container-wrapper[data-name="comments"] .#{$alias}item-comments {
		display: block !important;
	}

	#plugin-container-wrapper {
		width: 990px;
		position: absolute;
		top: 0px;
		bottom: 0px;
		left: 50%;
		margin-left: -480px;
		z-index: 999999;
		background: #fff;
		display: grid;
		grid-template-columns: 680px 300px;
		grid-column-gap: 0px;

		.#{$alias}item-name {
			font-size: 18px;
			white-space: nowrap;
			overflow: hidden;
			text-overflow: ellipsis;
			width: 100%;
			padding-bottom: 10px;
		}

		#plugin-left-col {
			padding: 20px;
			a.#{$alias}item-banner {
				border: 1px solid #E0E0E0;
				background: #F5F5F5;
				display: block;
				text-align: center;
				margin-bottom: 0px;
				padding: 8px 5px 6px 5px;
				border-bottom: 0;
			}
			#plugin-information-tabs {
				margin-left: -20px;
				padding-left: 20px;
				border-right: 1px solid #E0E0E0;

				a:focus {
					box-shadow: none;
				}
			}

			.#{$alias}item-attributes,
			.#{$alias}item-comments {
				display: none;
			}
			.#{$alias}item-desc {
				display: none;

				* {
					box-sizing: border-box;
				}
			}
			.#{$alias}item-content {
				@extend %nice-scroll;
				height: 300px;
				overflow: auto;
				overflow-x: hidden;
				margin-top: 20px;
			}

			.#{$alias}item-action-buttons {
				border: 1px solid #E0E0E0;
				background: #F5F5F5;
				border-top: 0;
				padding: 10px 0 20px 0;
				text-align: center;
			}

			.#{$alias}product-comment {
				display: grid;
				grid-template-columns: 80px 1fr;
				grid-column-gap: 15px;
				margin-bottom: 20px;
				font-size: 12px;
				border-bottom: 1px solid #E0E0E0;
				padding-bottom: 20px;

				img {
					max-width: 100%;
					margin-top: 4px;
				}
				h4 {
					padding: 0;
					margin: 0;
					margin-bottom: 10px;
					font-size: 13px;
				}
			}
		}


		#plugin-right-col {
			padding: 10px;
			padding-left: 0;
			.#{$alias}item-sales {
				border: 1px solid #E0E0E0;
				background: #F5F5F5;
				padding: 10px 10px 15px 25px;
				font-size: 16px;
				margin-top: 30px;

				strong {
					font-size: 22px;
					vertical-align: middle;
					display: inline-block;
				}
				span {
					margin-left: 10px;
					vertical-align: middle;
					display: inline-block;
				}
			}

			.#{$alias}author {
				display: grid;
				grid-template-columns: 60px 1fr;
				grid-column-gap: 15px;
				border: 1px solid #E0E0E0;
				background: #F5F5F5;
				padding: 10px 10px 15px 10px;
				margin-top: 30px;

				img {
					width: 60px;
				}

				h5 {
					padding: 0;
					margin: 0;
					margin-bottom: 10px;
					font-size: 18px;

					a {
						color: #1a1a1a;
						text-decoration: none;

						&:hover {
							color: #5E35B1;
						}
					}
				}
				strong {
					margin-left: 5px;
				}
			}

			.#{$alias}item-rating {
				border: 1px solid #E0E0E0;
				background: #F5F5F5;
				padding: 10px 25px 15px 25px;
				font-size: 16px;
				margin-top: 30px;

				strong {
					font-size: 13px;
					vertical-align: middle;
					display: inline-block;
				}
				.star-rating {
					margin-left: 10px;
					vertical-align: middle;
					display: inline-block;
				}
				.rating-num {
					font-size: 12px;
					padding-top: 3px;
					color: #999999;
				}
			}

			.#{$alias}license-types {
				margin-top: 50px;
				> div {
					position: relative;
					padding-bottom: 10px;
				}
				h3 {
					padding: 0;
					margin: 0;
					padding-top: 10px;
				}
				.license-price {
					position: absolute;
					top: 5px;
					right: 15px;
					font-size: 22px;
					font-weight: bold;
					sup {
						position: relative;
						bottom: -2px;
						font-size: 16px;
					}
				}
				.single-site {
					border-bottom: 1px solid #EEEEEE;
				}
				.extended-site {
					padding-top: 20px;

					.license-price {
						top: 25px;
					}
				}
				p {
					font-size: 12px;
					padding-right: 30px;
				}
				a {
					text-decoration: none;
					&:hover {
						text-decoration: underline;
					}
				}
			}
		}
	}

	.#{$alias}search-form {
		position: relative;
		margin: 12px 0 25px;
		padding: 40px 40px;
		width: 100%;
		box-shadow: 0 1px 1px rgba(0,0,0,.04);
		border: 1px solid #e5e5e5;
		background: #fff;
		color: #555;
		font-size: 13px;
		box-sizing: border-box;
		text-align: center;

		.#{$alias}advanced {
			margin-top: 10px;
			padding-top: 10px;
			overflow: hidden;
			height: 35px;

			&.is_opened {
				height: auto;

				.#{$alias}advanced-actions {
					a:first-child {
						display: none;
					}
					a:last-child {
						display: block;
					}
				}
			}

			.#{$alias}advanced-actions {
				border-bottom: 1px solid #EEEEEE;
				margin-bottom: 30px;
				padding-bottom: 10px;
				a {
					display: none;
					color: #757575;
					font-size: 14px;
					text-decoration: none;
					text-transform: uppercase;
					vertical-align: middle;
					font-weight: 700;

					span {
						position: relative;
						top: -2px;
						margin-right: 5px;
					}

					&:hover {
						color: #5E35B1;

						span {
							color: #5E35B1;
						}
					}
				}

				a:first-child {
					display: block;
				}
			}

		}

		.the-keyword {
			text-align: center;
			font-size: 0;

			input {
				width: 800px;
				font-size: 16px;
				padding: 15px 30px;
				padding-bottom: 16px;
				display: inline-block;
				vertical-align: middle;
				border: 1px solid #BDBDBD;
				border-top-left-radius: 4px;
				border-bottom-left-radius: 4px;
				margin: 0;
				border-right: 0;
			}

			select {
				background: url(data:image/svg+xml;base64,) no-repeat 100% 50%;
				background-position: 95% 50%;
				padding: 12px 30px 12px 30px;
				height: auto;
				display: inline-block;
				vertical-align: middle;
				margin-left: 0px;
				-moz-appearance: none;
				-webkit-appearance: none;
				-webkit-border-radius: 0px;
				appearance: none;
				outline-width: 0;
				border: 1px solid #BDBDBD;
				border-top-right-radius: 4px;
				border-bottom-right-radius: 4px;
			}
		}

		.#{$alias}form-row:not(:first-child) {
			border-right: 1px solid #F5F5F5;
			display: inline-block;
			padding: 5px 35px;
			text-align: left;
			vertical-align: middle;

			&:last-child {
				border: none;
			}

			.#{$alias}form-note {
				font-size: 11px;
				color: #9E9E9E;
			}
			span {
				font-weight: bold;
				margin-right: 5px;
				display: block;
				margin-bottom: 5px;
				text-transform: uppercase;
			}
			input[type="text"] {
				padding: 8px 15px 8px 15px;
				border: 1px solid #BDBDBD;
				border-radius: 4px;
				min-width: 300px;
			}
			select {
				background: url(data:image/svg+xml;base64,) no-repeat 100% 50%;
				background-position: 95% 50%;
				padding: 8px 15px 8px 15px;
				height: auto;
				display: inline-block;
				vertical-align: middle;
				margin-left: 0px;
				-moz-appearance: none;
				-webkit-appearance: none;
				-webkit-border-radius: 0px;
				appearance: none;
				outline-width: 0;
				border: 1px solid #BDBDBD;
				border-radius: 4px;
				width: 150px;
			}
		}

		input[type="submit"] {
			margin-top: 20px;
			font-size: 20px;
			text-transform: uppercase;
			padding: 8px 25px;
		}
	}
}