%centered {
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
}

%reset-list {
	&, li {
		margin: 0;
		padding: 0;
	}
	li {
		list-style: none;
	}
}
%li-inline {
	list-style: none;
	display: inline-block;
}
%input {
	border: none 0;
	border-radius: $radius;
	height: 38px;
	line-height: 38px;
	font-size: 13px;
	padding: 0 16px;
	display: inline-block;
}
%button {
	font-size: 13px;
	border: none 0;
	height: 38px;
	line-height: 38px;
	text-align: center;
	padding: 0 27px;
	border-radius: 19px;
	color: $white;
	display: inline-block;
}
%btn-primary, %btn-secondary, %btn-danger, %btn-warning, %btn-success {
	@extend %button;
	color: $white;
	&:hover,
	&:focus,
	&.focus {
		opacity: 0.8;
	}
}
%btn-primary {
	@include hGradient($primary, lighten($primary,5));
}
%btn-secondary {
	@include hGradient($secondary, lighten($secondary,5));
}
%btn-danger {
	@include hGradient($danger, lighten($danger,5));
}
%btn-warning {
	@include hGradient($warning, lighten($warning,5));
}
%btn-success {
	@include hGradient($success, lighten($success,5));
}
%tag {
	height: 22px;
	line-height: 22px;
	border-radius: 11px;
	font-size: 11px;
	padding: 0 16px;
	color: $bodytext;
	background: $gray;
	display: inline-block;
}
%wallpaper {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
}
%nice-scroll {
	padding-right: 12px;
	&::-webkit-scrollbar {
		width: 6px;
		height: 8px;
		background: $gray;
	}
	&::-webkit-scrollbar-thumb {
		border-radius: 4px;
		background: $dark;
		border: none 0;
		background-clip: content-box;
		&:hover {
			background: rgba($dark,.8);
		}
	}
	&::-webkit-scrollbar-track {
		border-radius: 2px;
	}
}
%custom-checkbox {
	position: absolute; // take it out of document flow
	opacity: 0; // hide it
	& + label {
		position: relative;
		cursor: pointer;
		padding: 0;
	}
	& + label:before {
		content: '';
		margin-right: 10px;
		display: inline-block;
		vertical-align: text-top;
		width: 16px;
		height: 16px;
		background: white;
		border: solid 1px lighten($bodytext,50);
	}
	&:hover + label:before {
		background: lighten($bodytext,65);
	}
	&:focus + label:before {
		box-shadow: -2px 0px 0px 1px rgba($bodytext, 0.2);
	}
	&:checked + label:before {
		background: $white;
	}
	&:checked + label:after {
		content: '';
		position: absolute;
		left: 3px;
		top: 15px;
		background: $bodytext;
		width: 2px;
		height: 2px;
		box-shadow: 
		2px 0 0 $bodytext,
		4px 0 0 $bodytext,
		4px -2px 0 $bodytext,
		4px -4px 0 $bodytext,
		4px -6px 0 $bodytext,
		4px -8px 0 $bodytext;
		transform: rotate(45deg);
	}
	&:disabled + label {
		color: #b8b8b8;
		cursor: auto;
	}
	&:disabled + label:before {
		box-shadow: none;
		background: #ddd;
	}
}
%custom-checkbox-round {
	@extend %custom-checkbox;
	&:focus {
		& + label {
			&:before {
				box-shadow: none;
			}
		}
	}
	&:not(:checked) + label {
		border-color: darken($gray2,15);
		&:before {
			content: "-";
			font-size: 32px;
			line-height: 13px;
			text-align: center;
			color: darken($gray2,15);
		}
	}
	&:checked + label:after {
		left: 6px;
		top: 11px;
	}
	&+label {
		width: 26px;
		height: 26px;
		line-height: 26px;
		border: solid 2px $secondary;
		border-radius: 50%;
		left: -14px;
		top: -14px;
		background: $white;
		&:before {
			border: none 0;
			border-radius: 50%;
			left: 3px;
			top: 3px;
			position: absolute;
		}
	}
}