@font-face {
  font-family: 'SmartSeo-icon';
  src:
    url('fonts/SmartSeo-icon.ttf?qqmt01') format('truetype'),
    url('fonts/SmartSeo-icon.woff?qqmt01') format('woff'),
    url('fonts/SmartSeo-icon.svg?qqmt01#SmartSeo-icon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="SmartSeo-icon"], [class*=" SmartSeo-icon"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'SmartSeo-icon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

